import PropTypes from 'prop-types';

const InfoCard = props => (
  <>
    <div className="info-card-image" />
    <div className="info-card-body">
      <h2>{props.title}</h2>
    </div>
  </>
);

InfoCard.propTypes = {
  img: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired
};

export default InfoCard;




